@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato/latoregular.woff2") format("woff2"), url("../fonts/lato/latoregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
}

a {
  text-decoration: none;
}
a:focus-visible {
  outline: 3px dotted #f06351;
  outline-offset: 2px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.about {
  max-width: 1440px;
  margin: 0 auto;
  padding: 20px 20px 49px 20px;
}
@media (min-width: 768px) {
  .about {
    padding: 45px 30px 47px 30px;
  }
}
@media (min-width: 1440px) {
  .about {
    padding: 71px 110px 69px 110px;
  }
}

.about__title {
  max-width: 600px;
  margin: 0 0 40px 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}
@media (min-width: 768px) {
  .about__title {
    font-size: 60px;
    line-height: 60px;
  }
}
@media (min-width: 768px) {
  .about__title {
    margin: 0 0 32px 0;
  }
}

.about__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  counter-reset: number 0;
  gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
}
@media (min-width: 768px) {
  .about__list {
    column-gap: 140px;
    grid-template-columns: repeat(auto-fill, 245px);
    row-gap: 37px;
  }
}
@media (min-width: 1440px) {
  .about__list {
    column-gap: 80px;
  }
}

.about__item {
  position: relative;
  min-height: 60px;
  padding: 2px 0 2px 80px;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
  counter-increment: number;
}
.about__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 25px;
}
.about__item--healthy::before {
  background-image: url("../img/svg/healthy-icon.svg");
}
.about__item--making::before {
  background-image: url("../img/svg/making-icon.svg");
}
.about__item--feeding::before {
  background-image: url("../img/svg/feeding-icon.svg");
}
.about__item--effect::before {
  background-image: url("../img/svg/effect-icon.svg");
}
@media (min-width: 768px) {
  .about__item {
    padding: 151px 0 57px 0;
    font-size: 16px;
    line-height: 24px;
  }
  .about__item::before {
    top: 40px;
    width: 80px;
    height: 80px;
  }
  .about__item::after {
    content: counter(number);
    position: absolute;
    top: 0;
    right: 0;
    z-index: -5;
    font-size: 280px;
    line-height: 280px;
    font-family: "Oswald", "Arial", sans-serif;
    color: #f2f2f2;
  }
  .about__item:first-child::after {
    right: 20px;
  }
}

.additional {
  max-width: 1440px;
  margin: 0 auto;
  padding: 25px 20px 35px 20px;
}
@media (min-width: 768px) {
  .additional {
    padding: 68px 30px 74px 30px;
  }
}
@media (min-width: 1440px) {
  .additional {
    padding: 75px 110px 86px 110px;
  }
}

.additional__subject {
  position: relative;
  width: max-content;
  max-width: 300px;
  margin: 0 0 35px -20px;
  padding: 0 21px 0 20px;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  background-color: #ffffff;
}
.additional__subject::after {
  content: "";
  position: absolute;
  top: 15px;
  left: 50%;
  z-index: -1;
  width: 200vw;
  height: 2px;
  background-color: #68b738;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .additional__subject {
    max-width: 420px;
    margin: 0 0 80px 0;
    padding: 0 36px 0 37px;
    font-size: 36px;
    line-height: 36px;
  }
  .additional__subject::after {
    top: 22px;
  }
}
@media (min-width: 1440px) {
  .additional__subject {
    margin: 0 0 79px -40px;
    padding: 0 35px 0 40px;
  }
}

.additional__wrapper {
  display: grid;
  row-gap: 35px;
}
@media (min-width: 768px) {
  .additional__wrapper {
    row-gap: 70px;
  }
}
@media (min-width: 1440px) {
  .additional__wrapper {
    align-items: start;
    column-gap: 80px;
    grid-template-columns: 1fr 245px;
  }
}

.additional__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .additional__list {
    box-shadow: 0 0 0 1px #cdcdcd inset;
  }
}
@media (min-width: 1440px) {
  .additional__list {
    box-shadow: none;
  }
}

.additional__item {
  position: relative;
  display: grid;
  padding: 11px 0 17px 0;
}
.additional__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #cdcdcd;
}
.additional__item:last-child::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #cdcdcd;
}
@media (min-width: 768px) {
  .additional__item {
    padding: 26px 38px 28px 38px;
    column-gap: 40px;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 22px;
  }
}
@media (min-width: 1440px) {
  .additional__item {
    align-items: center;
    padding: 15px 0 17px 0;
    gap: 40px;
    grid-template-columns: 1fr 285px 244px;
  }
}

.additional__subtitle {
  margin: 0 0 13px 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #222222;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .additional__subtitle {
    margin: 0;
    font-size: 20px;
    line-height: 20px;
  }
}

.additional__points {
  display: grid;
  margin: 0 0 15px 0;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
  column-gap: 30px;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 15px;
}
@media (min-width: 768px) {
  .additional__points {
    margin: 3px 6px 0 0;
    font-size: 16px;
    line-height: 20px;
    gap: 15px;
    grid-template-columns: 1fr 78px;
  }
}
@media (min-width: 1440px) {
  .additional__points {
    margin: 6px 0 0 0;
    column-gap: 30px;
    grid-template-columns: 1fr minmax(42px, max-content);
  }
}

.additional__term {
  max-width: 150px;
  padding: 0;
}
@media (min-width: 768px) {
  .additional__term {
    justify-self: end;
  }
}
@media (min-width: 1440px) {
  .additional__term {
    min-width: 131px;
    justify-self: start;
  }
}

.additional__definition {
  margin: 0;
  padding: 0;
  justify-self: end;
}
@media (min-width: 1440px) {
  .additional__definition {
    justify-self: start;
  }
}

.additional__button {
  padding: 10px 20px;
  font-size: 16px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .additional__button {
    grid-column: 1/-1;
  }
}
@media (min-width: 1440px) {
  .additional__button {
    grid-column: 3/-1;
  }
}

.additional__bonus {
  position: relative;
  margin: 0;
  padding: 187px 59px 43px 57px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Lato", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  background-color: rgba(104, 183, 56, 0.85);
}
.additional__bonus::before, .additional__bonus::after {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
}
.additional__bonus::before {
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-image: url("../img/background/bonus-cat.jpg");
  background-position: center;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .additional__bonus::before {
    background-image: url("../img/background/bonus-cat@2x.jpg");
  }
}
.additional__bonus::after {
  top: 59px;
  left: 50%;
  width: 78px;
  height: 80px;
  background-image: url("../img/svg/gift-icon.svg");
  transform: translateX(-50%);
}
@media (min-width: 768px) and (max-width: 1439px) {
  .additional__bonus {
    padding: 71px 124px 69px 422px;
    text-align: start;
  }
  .additional__bonus::before {
    background-image: url("../img/background/bonus-cat-tablet.jpg");
  }
}
@media (min-width: 768px) and (max-width: 1439px) and (min-resolution: 2dppx) {
  .additional__bonus::before {
    background-image: url("../img/background/bonus-cat-tablet@2x.jpg");
  }
}
@media (min-width: 768px) and (max-width: 1439px) {
  .additional__bonus::after {
    top: 50%;
    left: 78px;
    width: 80px;
    height: 82px;
    transform: translateY(-50%);
  }
}
@media (min-width: 1440px) {
  .additional__bonus {
    padding: 185px 43px 42px 41px;
  }
}

.application {
  max-width: 1440px;
  margin: 0 auto;
  padding: 28px 20px 32px 20px;
}
@media (min-width: 768px) {
  .application {
    padding: 64px 70px 72px 70px;
  }
}
@media (min-width: 1440px) {
  .application {
    padding: 75px 110px 82px 110px;
  }
}

.application__title {
  max-width: 600px;
  margin: 0 0 37px 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 768px) {
  .application__title {
    font-size: 60px;
    line-height: 60px;
  }
}
@media (min-width: 768px) {
  .application__title {
    margin: 0 0 54px 0;
  }
}
@media (min-width: 1440px) {
  .application__title {
    margin: 0 0 56px 0;
  }
}

.application__description {
  max-width: 230px;
  margin: 0 0 37px 0;
  color: #444444;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .application__description {
    max-width: 330px;
    margin: 0 0 66px 0;
    font-size: 20px;
    line-height: 30px;
  }
}

.application__subject {
  position: relative;
  width: max-content;
  max-width: 228px;
  margin: 0 0 0 -23px;
  padding: 0 23px;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  text-transform: none;
  background-color: #ffffff;
}
.application__subject::after {
  content: "";
  position: absolute;
  top: 15px;
  left: 50%;
  z-index: -1;
  width: 200vw;
  height: 2px;
  background-color: #68b738;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .application__subject {
    max-width: 600px;
    margin: 0 0 0 -40px;
    padding: 0 40px;
    font-size: 36px;
    line-height: 36px;
  }
  .application__subject::after {
    top: 22px;
  }
}

.application__form-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 24px;
}
@media (min-width: 768px) {
  .application__form-wrapper {
    gap: 65px;
  }
}

.application__fieldset {
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  text-transform: uppercase;
  border: 0;
}
.application__fieldset--contacts .application__subject {
  margin-bottom: 35px;
}
.application__fieldset--contacts .application__contact {
  column-gap: 10px;
}
.application__fieldset--comments .application__subject {
  margin-bottom: 37px;
}
.application__fieldset--additional .application__subject {
  margin-bottom: 34px;
}
@media (min-width: 768px) {
  .application__fieldset {
    font-size: 20px;
    line-height: 30px;
  }
  .application__fieldset--contacts .application__subject {
    margin-bottom: 76px;
  }
  .application__fieldset--contacts .application__contact {
    column-gap: 20px;
  }
  .application__fieldset--comments .application__subject {
    margin-bottom: 76px;
  }
  .application__fieldset--additional .application__subject {
    margin-bottom: 57px;
  }
}
@media (min-width: 1440px) {
  .application__fieldset--half {
    width: calc(50% - 40px);
  }
  .application__fieldset--contacts .application__list {
    align-items: center;
    margin-bottom: 2px;
    column-gap: 78px;
    grid-template-columns: repeat(2, 571px);
  }
  .application__fieldset--comments {
    margin-bottom: 2px;
  }
}

.application__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 14px;
}
.application__list--radio {
  position: relative;
  padding: 22px 0;
  gap: 13px;
}
.application__list--radio::before, .application__list--radio::after {
  content: "";
  position: absolute;
  left: -20px;
  width: calc(100% + 40px);
  height: 1px;
  background-color: #d9d9d9;
}
.application__list--radio::before {
  top: 0;
}
.application__list--radio::after {
  bottom: 0;
}
.application__list--checkbox {
  gap: 12px;
}
@media (min-width: 768px) {
  .application__list {
    gap: 20px;
  }
  .application__list--radio {
    margin: 9px 0 11px 0;
    padding: 24px 28px;
    box-shadow: 0 0 0 1px #e7e7e7;
    gap: 13px;
  }
  .application__list--radio::before, .application__list--radio::after {
    display: none;
  }
  .application__list--checkbox {
    column-gap: 123px;
    grid-template-columns: repeat(auto-fill, 221px);
    row-gap: 32px;
  }
}
@media (min-width: 1440px) {
  .application__list--radio {
    margin: 0 0 6px 0;
  }
  .application__list--checkbox {
    column-gap: 104px;
  }
}

.application__contact {
  position: relative;
  display: grid;
  align-items: center;
  column-gap: 14px;
  grid-template-columns: max-content 1fr;
}

.application__textarea {
  display: flex;
}

.application__sign {
  max-width: 95px;
  color: #444444;
}
@media (min-width: 768px) {
  .application__sign {
    max-width: 200px;
  }
}

.application__area {
  width: 100%;
  padding: 14px 20px 13px 20px;
  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  border: none;
  border-radius: 0;
  outline: 1px solid #e7e7e7;
}
.application__area--mail, .application__area--phone {
  padding: 14px 50px 13px 16px;
}
.application__area--textarea {
  min-height: 150px;
  padding: 9px 12px 9px 15px;
  resize: none;
}
.application__area::placeholder {
  color: #444444;
  opacity: 1;
}
.application__area:hover {
  outline-color: #cdcdcd;
}
.application__area:focus-visible {
  outline-width: 2px;
  outline-color: #444444;
}
.application__area:focus-visible + .application__icon {
  background-color: #68b738;
}
.application__area:disabled {
  outline-color: #e7e7e7;
  opacity: 0.3;
}
.application__area:disabled + .application__icon {
  opacity: 0.3;
}
.application__area:invalid:not(:placeholder-shown) {
  outline-width: 2px;
  outline-color: #ff8282;
}
.application__area:invalid:not(:placeholder-shown) + .application__icon {
  background-color: #ff8282;
}
.application__area::-webkit-contacts-auto-fill-button {
  position: absolute;
  left: 0;
  display: none;
  visibility: hidden;
  pointer-events: none;
}
@media (min-width: 768px) {
  .application__area {
    padding: 11px 20px;
    font-size: 20px;
    line-height: 30px;
  }
  .application__area--mail, .application__area--phone {
    padding-right: 50px;
  }
  .application__area--textarea {
    min-height: 174px;
    padding: 10px 19px;
  }
}
@media (min-width: 1440px) {
  .application__area--mail, .application__area--phone {
    padding-right: 60px;
  }
}

.application__icon {
  position: absolute;
  top: 50%;
  right: 15px;
  width: 20px;
  height: 20px;
  background-color: #000000;
  transform: translateY(-50%);
  mask-repeat: no-repeat;
}
.application__icon--mail {
  mask-image: url("../img/svg/email-icon.svg");
}
.application__icon--phone {
  mask-image: url("../img/svg/phone-icon.svg");
}
@media (min-width: 1440px) {
  .application__icon {
    right: 19px;
  }
}

.application__control {
  display: grid;
  cursor: pointer;
  gap: 15px;
  grid-template-columns: 30px 1fr;
}
.application__control:active {
  opacity: 0.6;
}
.application__control:has(input:disabled) {
  cursor: default;
  opacity: 1;
}
@media (min-width: 768px) {
  .application__control {
    gap: 20px;
    grid-template-columns: 40px 1fr;
  }
}

.application__value {
  display: block;
  padding: 6px 0;
  color: #222222;
}
@media (min-width: 768px) {
  .application__value {
    padding: 5px 0;
  }
}

.application__control-input {
  position: relative;
  width: 30px;
  height: 30px;
  margin: 0;
  border: 0;
  outline: none;
  cursor: pointer;
  appearance: none;
}
.application__control-input::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
}
.application__control-input[type=radio]::after {
  border-radius: 50%;
}
.application__control-input[type=radio]:checked::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 13px;
  height: 13px;
  background-color: #68b738;
  border-radius: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.application__control-input[type=checkbox]:checked::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 18px;
  height: 14px;
  background-image: url("../img/svg/mark-checkbox.svg");
  background-repeat: no-repeat;
  transform: translateX(-50%) translateY(-50%);
}
.application__control-input:hover::after {
  border-color: #bababa;
}
.application__control-input:focus-visible::after {
  border-width: 2px;
  border-color: #444444;
}
.application__control-input:disabled::after {
  border-color: #cdcdcd;
  opacity: 0.3;
}
.application__control-input:disabled ~ .application__value {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .application__control-input {
    width: 40px;
    height: 40px;
  }
  .application__control-input::after {
    width: 40px;
    height: 40px;
  }
  .application__control-input[type=radio]:checked::before {
    width: 18px;
    height: 18px;
  }
  .application__control-input[type=checkbox]:checked::before {
    width: 24px;
    height: 19px;
    background-size: 24px;
  }
}

.application__submission {
  margin-top: 34px;
}
@media (min-width: 768px) {
  .application__submission {
    display: flex;
    align-items: center;
    margin-top: 54px;
  }
}
@media (min-width: 1440px) {
  .application__submission {
    margin-top: 61px;
  }
}

.application__button {
  width: 100%;
  padding: 10px 20px;
  font-size: 16px;
  line-height: 20px;
}
.application__button:focus-visible {
  outline: 3px dotted #f06351;
  outline-offset: 2px;
}
@media (min-width: 768px) {
  .application__button {
    max-width: 282px;
    padding: 13px 45px;
    font-size: 20px;
    line-height: 25px;
  }
}
@media (min-width: 1440px) {
  .application__button {
    max-width: 568px;
  }
}

.application__stint {
  display: block;
  margin: 21px 0 0 0;
  line-height: 16px;
  font-family: "Lato", "Arial", sans-serif;
  text-align: center;
}
@media (min-width: 768px) {
  .application__stint {
    margin: 0 0 8px 62px;
    font-size: 16px;
  }
}
@media (min-width: 1440px) {
  .application__stint {
    margin: 0 0 0 82px;
  }
}

.button {
  display: block;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
