.slider {
  display: grid;
  width: 280px;
  height: 256px;
  grid-template-columns: var(--curtain-place, 50%) 1fr;
}
@media (min-width: 768px) {
  .slider {
    width: 560px;
    height: 512px;
  }
}

.slider__fraction {
  display: flex;
  overflow: hidden;
  grid-row: 1/2;
}
.slider__fraction--before {
  grid-column: 1/2;
}
.slider__fraction--after {
  justify-content: flex-end;
  grid-column: 2/3;
}

.slider__range {
  z-index: 1;
  height: 100%;
  margin: 0;
  cursor: pointer;
  opacity: 0;
  grid-column: 1/-1;
  grid-row: 1/2;
}
.slider__range:hover + .slider__control {
  color: #68b738;
}
.slider__range:active + .slider__control {
  color: rgba(104, 183, 56, 0.6);
}
.slider__range:focus-visible + .slider__control::before {
  outline: 3px dotted #f06351;
  outline-offset: 2px;
}

.slider__control {
  position: relative;
  width: 4px;
  height: 100%;
  color: #666666;
  background-color: currentColor;
  border-radius: 2px;
  transform: translateX(50%);
  grid-column: 1/2;
  grid-row: 1/2;
  justify-self: end;
}
.slider__control::before, .slider__control::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.slider__control::before {
  width: 40px;
  height: 40px;
  color: inherit;
  background-color: #ffffff;
  border: 2px solid currentColor;
  border-radius: 50%;
}
.slider__control::after {
  width: 24px;
  height: 18px;
  background-color: currentColor;
  mask-image: url("../img/svg/slider-arrow.svg");
  mask-repeat: no-repeat;
}